

































































































































    declare global {
        interface Window{ PrismicJS: any, 
            tns: any
            PrismicDOM: any, PhotoSwipe: any, PhotoSwipeUI_Default: any
        }
    }

    import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	//import Galleria from 'galleria';
	
	Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class VideoStream extends Vue { 
	    PrismicDOM = window.PrismicDOM;
		viewEvent: boolean = false;
		photos: any[] = [];
		current: any = '';
        slider: any = null;
        combinedImages: any = [];
        viewEventGallery: boolean = true;

        @Watch('combinedImages', {deep: true}) _iniSlides(){
            setTimeout(()=>
            this.slider = window.tns({
                container: '.gallery-slider',
                //slideBy: 'page',
                autoplay: true,
                controls: false,
                nav: false,
                arrowKeys: false,
                autoPlayHoverPause: true,
                autoplayButtonOutput: false,
                speed: 950,
                items:1.3,
                gutter:5,
                /*center:true,*/
                //edgePadding: 50,
                responsive:{
                    1024:{items:3.3, /*edgePadding: 100*/}
                }
                //mode: 'gallery',
                //autoplayTimeout: 9000
            }),5);
        }


		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm._getPage(); });
		}
		
        
        _viewEvent(p:any ) {
			this.current = p;
            this.slider.pause();
			this.viewEvent = true;
		}

        _closeModal() {
            this.viewEvent = false;
            this.slider.play();
        }

		async _getPage() {
			var api = await _Prismic();
			let response = await api.query(window.PrismicJS.Predicates.at('document.type', 'media'),
				{ fetch:[ 'media.body' ]}
			);
			//this.photos = response.results[0].data.body;
            response.results[0].data.body.map(d => this.combinedImages = [...this.combinedImages, ...d.items]);
		}

		_scrollTo(id: string) {
			var el = document.getElementById(id);
			el.scrollIntoView({behavior: 'smooth'});
		}
}
