








































































     declare global {
        interface Window{ PrismicJS: any, PrismicDOM: any }
    }	

	import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	import Devotional from '@/components/devotional.vue';
	import Categories from '@/components/categories.vue';
	
	Component.registerHooks(['beforeRouteEnter']);

	@Component({components: { Devotional, Categories }})
	export default class Devotionals extends Vue {
		prismicDOM = window.PrismicDOM; 
        mostRecent: any = {};	
		currentCategory: string = "";
		sermons: any = [];
		viewDevotion: boolean = false;
		devotionals: any = [];
		currentDevotion: any = {};
		page: number = 1;

        overlay = 'linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),';


		@Watch('currentCategory') async _updateCategory() {
			this.devotionals = [];
			this._getDevotional()
		}

		loadMore() {
			this.page++;
			this._getDevotional();
		}
			
		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm._getDevotional(); });
		}

		_navigateTo(uid: string) {
			this.$router.push({ path: 'devotionals', params:{ uid } }).catch(err => {});
		}

		async _getDevotional() {
			var api = await _Prismic();
			var response = await api.query(
				this.currentCategory ? window.PrismicJS.Predicates.fulltext('my.devotional.category', this.currentCategory) :
				 window.PrismicJS.Predicates.at('document.type', 'devotional'),
				{ fetch:[
					'devotional.title', 
					/*'devotional.image',*/
					'devotional.category',
					'devotional.date',
					'devotional.verses'
				],
					pageSize : 9, page : this.page 
			});
			this.devotionals = [...this.devotionals, ...response.results];
			if(this.mostRecent.data == null) this.mostRecent = this.devotionals[0];
		}

        backgroundImage(url) {
            return this.overlay + 'url("'+ url +'")';
        }


}
