


























































































	import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';

	declare global {
		interface Window { PrismicJS: any, videojs: any, tns: any, PrismicDOM: any }
	}

	Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class Home extends Vue {
        PrismicDOM = window.PrismicDOM;
		viewNewsletter: boolean = false;
        sermon: any = {};
        devotional: any = {};
		home: any = {};
        player: any = null;
        viewPlayer = false;
        slider:any = null;

        @Watch('home', {deep: true}) _iniSlides(){
            setTimeout(()=>
            this.slider = window.tns({
                container: '.my-slider',
                slideBy: 'page',
                autoplay: true,
                controls: false,
                nav: false,
                arrowKeys: true,
                autoPlayHoverPause: true,
                autoplayButtonOutput: false,
                speed: 950,
                mode: 'gallery',
                autoplayTimeout: 9000
            }),5);
        }
			
		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm._getPage(); vm._getSermons(); });
		}

        mounted(){
            this.player = window.videojs('latestsermon', {
                controls: true, responsive:true,
                controlBar: {fullscreenToggle: false, pictureInPictureToggle: false}
            });
        }

        beforeDestroy() {
			if(this.player) this.player.dispose();
		}

		async _getSermons() {

			var api = await _Prismic();;
			this.sermon = (await api.query(
                window.PrismicJS.Predicates.at('document.type', 'sermon'), 
                { pageSize : 1, page : 1, fetch:['sermon.video']}
            )).results[0];

			this.devotional = (await api.query(
                window.PrismicJS.Predicates.at('document.type', 'devotional'), 
                { pageSize : 1, page : 1, fetch:['devotional.title'] }
            )).results[0];
		}

        showPlayer() {
            this.player.src({'src': this.sermon.data.video.url, 'type':"video/mp4"});
            this.player.play();
            this.viewPlayer = true;
        }

        closePlayer() {
            this.player.pause();
            this.viewPlayer = false;
        }

		async _getPage() {
			var api = await _Prismic();
			this.home = (await api.getSingle('home')).data;
            var slides = this.home.slides.map(o =>{
                var url = '';
                switch(o.type){
                    case 'devotion': { url = '/devotionals'; break; }
                    case 'sermon': { url = '/media/sermons'; break; }
                    case 'event': { url='/events'; break; }
                    case 'product': { url='/store'; break; }
                    default: url='/';
                }
                return {url, image: o.image.url}
            });
            this.home = {...this.home, slides};
		}
}