import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/views/home.vue';
import AboutIndex from '@/views/aboutIndex.vue';
import About from '@/views/about-prod-3.vue';
import AboutDetail from '@/views/aboutDetail.vue';
import ProductStore from "@/views/horemowStore.vue";
import StoreIndex from '@/views/horemowStoreIndex.vue';
import Cart from '@/views/cart.vue';
import Events from '@/views/events.vue';
import Donate from '@/views/donations.vue';
import Media from "@/views/media.vue";
import Devotional from '@/views/devotional.vue';
import DevotionalIndex from '@/views/devotionalIndex.vue';
import ViewDevotional from '@/views/viewDevotional.vue';
import Contact from '@/views/contact.vue';
import Ministries from '@/views/ministries.vue';
import Location from '@/views/locations.vue';
import Leadership from '@/views/leadership.vue';
import Livestream from '@/views/livestream.vue';
import Sermons from '@/views/sermons.vue';
import Gallery from '@/views/gallery.vue';
import Evangelism from '@/views/evangelism.vue';
import AppDownload from '@/views/app-download.vue';
//import Audio from '@/views/audioStream.Audiovue';


Vue.use(Router);

const router = new Router({
	mode: 'history',
	base: process.env.BASE_URL,
	routes: [
		{   path: '/', component: Home},
		//{   path: '/about-us', component: AboutIndex},
		{   path: '/about-us', component: AboutIndex,
            children:[
                { path: '', component: About },
                { path: 'ministry', component: Ministries },
                { path: 'locations', component: Location },
                { path: 'leadership', component: Leadership },
                { path: ':id', component: AboutDetail }
            ]
        },
        {   path: '/media/sermons', component: Sermons },
        {   path: '/media/livestream', component: Livestream, props: { default:'', io_id: null}},
        {   path: '/media/gallery', component: Gallery },
        {   path: '/media/evangelism', component: Evangelism },
        {   path: '/media/app', component: AppDownload },
		{   path: '/contact-us', component: Contact},

		{	path: '/donate', component: Donate },
		//{   path:'/sermons', component: Media, props: {default: '', io_id: null }},
		{   path:'/devotionals', component: DevotionalIndex, 
			children:[
				{	path: '', component: Devotional },
				{	path:':uid', component: ViewDevotional }
			]
		},

		{   path:'/events', component: Events },
		//{   path:'/events/gallery', component: EventGallery },
		//{   path:'/collections/:id', name: 'media', component: Collections },
		{   path:'/store', component: StoreIndex, 
			children:[
				{	path: '',  component: ProductStore },
				/*{   path:'collections',  component: CollectionIndex,
					children:[
						{	path: ':id',  component: Collections }, 
						{	path: ':id/products/:prod', component: ProductView }
					]
				},*/
				{	path:'cart', component: Cart},
				/*{	path:'payment-success', component: Success},
				{	path:'payment-cancel', component: Cancel}*/

			]
		}
      ]
});

export default router;
