        is_paid: boolean = false;
		product: any = {};
		//categories: any = ['blouse', 'top', 'skirt', 'shoes', 'accessories', 'bracelet'];

        card: any = null;
        cardErrors: HTMLElement = null;

		/*beforeRouteEnter(to: Route, from: Route, next: Function) {
			//next((vm) => vm._getPage());
			next();
		}*/

		mounted() { this.init(); }

		async init() {
			this.stripe = await loadStripe('pk_test_7b7WnoyAoB1d8jL0yAijQfjV');
			let elements = this.stripe.elements();
            this.card = elements.create('card');
            this.card.mount('#card-element');
            this.card.on('change', ({error}) => {
                this.cardErrors = document.getElementById('card-errors');
                if(error){ this.cardErrors.textContent = error.message;
                }else{ this.cardErrors.textContent = '';}
            });
		}

        private async _getPaymentToken() {
            let { token, error } = await this.stripe.createToken(this.card);
            if(error){ this.cardErrors.textContent = error.message;
            }else{ this._paymentTokenHandler(token); }
        }

        private async _paymentTokenHandler(token) {
            const url = 'http://localhost:3000/process-payment';
            const headers = {"Content-Type": "application/json"};
            fetch(url, { method: 'post', body: JSON.stringify(token), headers, mode:'cors' })
                .then(response => response.json())
                .then(data => console.log(data));
        }

