import { loadStripe } from '@stripe/stripe-js';

export class Payment {
    private static instance: Payment;

    private stripe;
    private static API_KEY: string = 'pk_test_7b7WnoyAoB1d8jL0yAijQfjV';
    private card: any = null;
    private cardErrors: any = null;

    private style = {
        base: {
            iconColor: '#666ee8',
            color: '#31325f',
            fontWeight: 400,
            fontFamily: 'Rubik, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif',
            fontSmoothing: 'antialiased',
            fontSize: '12px',
            '::placeholder': { color: '#aab7c4' },
            ':-webkit-autofill': { color: '#666ee8'}
        }
    }

    private constructor(){
    }

    public static _getInstance(): Payment {
        if(!Payment.instance) Payment.instance = new Payment();
        return Payment.instance;
    }

    public async _initialize(): Promise<void> {
        this.stripe = await loadStripe(Payment.API_KEY);
        let elements = this.stripe.elements();

        this.card = elements.create('card',{style: this.style});
        this.card.mount('#card-element');
        this.card.on('change', ({error}) => {
            this.cardErrors = document.getElementById('card-errors');
            if(error){ this.cardErrors.textContent  = error.message; }
            else{ this.cardErrors.textContent = '';}
        });
    }

    public async processPayment(user: Object): Promise<void>{
        await this._getPaymentToken(user);
    }

    private async _getPaymentToken(user): Promise<void> {
        let { token, error }  = await this.stripe.createToken(this.card);
        if(error){this.cardErrors.textContent = error.message;}
        else{ this._paymentTokenHandler({user, token});}
    }

    private async _paymentTokenHandler(data: Object){
        this.cardErrors.textContent = '';
        const url = "http://localhost:3000/process-payment";
        const headers = { "Content-Type": "application/json"};
        fetch(url, {method: 'post', body: JSON.stringify(data), headers, mode: 'cors'})
            .then(response => response.json())
            .then(data => console.log(data));
    }
}
