





























	import { Component, Vue, Watch, Prop } from "vue-property-decorator";
	import APPLICATION from "@/webrtc/APPLICATION";
	import shortid from "shortid";
	import { Route } from 'vue-router';
	import db from "@/firebase";
	//import videojs from 'video.js';

	declare global {
		interface Window { videojs: any }
	}

	//Component.registerHooks(['beforeRouteLeave']);
	@Component
	export default class AudioStream extends Vue {

    

		@Prop({default: false}) readonly connectRadio: boolean;

		@Watch('connectRadio') _updateProp() {
			this.connectRadio? this._connect(): this._disconnect();
		}

        private servers:any = {
            'iceServers': [{'urls': 'stun:stun.l.google.com:19302'}, 
            {'urls': 'turn:numb.viagenie.ca','credential': 'beaver','username': 'webrtc.websitebeaver@gmail.com'}]
        };

		//private isPlaying: boolean = false;
		programs: any = {};
		get hasProgram() {
			return Object.keys(this.programs).length == 0;
		}

		private connections:any = {};
		private player: HTMLAudioElement;
		private uuid:string = shortid.generate();
		private paused:boolean = false;
		private streamEnd:boolean = false;
		private audioPlayer: any;
		private stream: MediaStream;

		isPlaying: boolean = false;

		public mounted() {
			this.audioPlayer = window.videojs('audioPlayer', {
				controls: true, responsive:true, liveui: true,
				controlBar:{fullscreenToggle: false, pictureInPictureToggle: false}
			});

			this.player = this.audioPlayer.tech().el();

			this.audioPlayer.controlBar.playToggle.on('click', (e) => {
				this.isPlaying? this._pause() : this._play();
			});


			db.AUDIO_SIGNAL.on("child_added", (data:any) => {
				data = JSON.parse(data.val().d);
				if(this.uuid == data.to) this.handleSignal(data);
			});

			//db.AUDIO_STARTED_SIGNAL.on("value",this.streamStarted);
			
		}

		async _connect() {
			let value = (await db.AUDIO_STARTED_SIGNAL.once('value')).val();
			if(value.started == 'yes') {
				var off = db.AUDIO_CONNECTED.push({uuid: this.uuid});
				off.remove();
				this.streamEnd = false;
			}
		}

		async _disconnect() {

		}

		_play() {
			this.player.play(); 
			this.isPlaying = true;
		}

		_pause() {
			this.player.pause; 
			this.isPlaying = false;
		}

		private streamStarted(data:any) {
			switch(data.val().started) {
				case "yes":{
					var off = db.AUDIO_CONNECTED.push({uuid: this.uuid});
					off.remove();
					this.streamEnd = false;
				}
				break;
				case "no":{
					this.streamEnd = true;
				}
				break;
				case "pause":{
					this.paused = true;
				}
				break;
				case "resume":{
					this.paused = false;
				}
				break;
			}
		}

		public getRemoteMediaStream(event:RTCTrackEvent) {
			//this.stream = event.streams[0];
			/*this.audioPlayer.srcObject = event.streams[0];
			this.audioPlayer.play();*/
			this.player.srcObject = event.streams[0];
			this.player.play()
			this.isPlaying = true;
		}

		private getPeerConnection(uuid:string) {
			if(uuid != this.uuid){
				if(this.connections[uuid]) return this.connections[uuid];
				var pc = new RTCPeerConnection(APPLICATION.servers);
				this.connections[uuid] = pc;
				pc.addEventListener("icecandidate", e => {
					var off = db.AUDIO_SIGNAL.push({d: JSON.stringify({"by": this.uuid, "to": uuid, "ice": e.candidate, "type":"ice"})});
					off.remove();
					
				});
				pc.addEventListener("track", e => this.getRemoteMediaStream(e));
				return pc;
			}
		}

		private handleSignal(data:any) {
			if(this.uuid != data.by){
				var pc = this.getPeerConnection(data.by);
				switch(data.type){
					case "offer":
						pc.setRemoteDescription(data.sdp)
							.then(() => pc.createAnswer())
							.then((answer:any) =>{
								pc.setLocalDescription(answer);
								var off = db.AUDIO_SIGNAL.push({d: JSON.stringify({"by": this.uuid, "to": data.by, "sdp":answer, "type": "answer"})});
								off.remove();				
							});
						break;
					case "ice":
						if(data.ice) pc.addIceCandidate(data.ice)
								.then(() => {})
								.catch((err:any) => {});
						break;
				}
			}
		}
}
