






























































































































































	import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	import db from "@/firebase";
	import shortid from "shortid";
	
    declare global {
		interface Window { PrismicJS: any, videojs: any, tns: any, PrismicDOM: any }
	}

	Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class LiveStream extends Vue {
		prismicDOM = window.PrismicDOM; 
		private viewProgram: boolean = false;
		private remoteVideo: HTMLVideoElement;
		private askQuestionModal: boolean = false;
        private question: any = {};
		private currentMedia: any = {title:'', date:''};
		private isPlaying: boolean = false;
		private sermonListM:HTMLElement;
		private sermonListD:HTMLElement;
		private scrollLeftBtn:HTMLElement;
		private scrollRightBtn:HTMLElement;
		private mouseDown:number = -1; 

		private sermonPlaylist:HTMLElement;
		private scrollUpBtn:HTMLElement;
		private scrollDownBtn:HTMLElement;

        private offerOptions: any ={ offerToReceiveVideo: 1, offerToReceiveAudio: 1 };
        private servers:any = {
            'iceServers': [{'urls': 'stun:stun.l.google.com:19302'}, 
            {'urls': 'turn:numb.viagenie.ca','credential': 'beaver','username': 'webrtc.websitebeaver@gmail.com'}]
		};
		

		programs: any = {};
		get hasProgram() {
			return Object.keys(this.programs).length == 0;
		}

		private audioVolume: number = 0.2;
		private connections:any = {};
		private player:any = null;
		private uuid:string = shortid.generate();
		private paused:boolean = false;
		private streamEnd:boolean = true; // false
		private stream: MediaStream;

		private isProgram: boolean = true;

		//private mouseDown:number = -1; 

		page: number = 1;
		total: number = 0;
        sermons: any = [];
		first:string = null;
		currentIndex: number = 0;

		slideItems: NodeListOf<Element>;
		slideCategory:  NodeListOf<Element>;
		categories: any = ['audio', 'video'];
		middleSection: any = [];
		newStyle: any = [];
        modalEnd: any = null;
        modalPause: any = null;

		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => vm._getPage());
		}

        @Watch('streamEnd') _toggleVideoModal(){
            if(this.streamEnd == true) this.modalEnd.open();
            else{this.modalEnd.close();}
        }

		public mounted() {
			// DO NOT DELETE this.init();
			db.VIDEO_SIGNAL.on("child_added", (data:any) => {
				data = JSON.parse(data.val().d);
				if(this.uuid == data.to) this.handleSignal(data);
			});

			db.VIDEO_STARTED_SIGNAL.on("value",this.streamStarted);

			this.player = window.videojs('video-id', {
				liveui: true, responsive:true, controlBar: {pictureInPictureToggle: false}
			});

            var ModalDialog = window.videojs.getComponent('ModalDialog');
            this.modalEnd = new ModalDialog(this.player, {temporary: false, uncloseable: true});
            this.modalEnd.addClass('not-started-modal');
            this.player.addChild(this.modalEnd);

            this.modalPause = new ModalDialog(this.player, {temporary: false, uncloseable: true});
            this.modalEnd.addClass('not-started-modal');
            this.player.addChild(this.modalPause);

            this.modalEnd.open();

			this.remoteVideo = this.player.tech().el();
			this.player.controlBar.playToggle.on('click', (e) => {
				this.isPlaying? this._pause() : this._play();
			});
		}

		async _getPage() {
			this._getPrograms();
		}

		async _getPrograms() {
			var response = (await _Prismic()).getSingle('program');
			response.then(d => this.programs = d.data.programs);
		}

		_sendQuestion() {
			var m = db.VIDEO_CHATS.push(JSON.stringify(this.question));
			m.remove();
            this.question = {};
			this.askQuestionModal = false;
		}


		beforeDestroy() {
			if(this.player) this.player.dispose();
		}

		_play() {
			this.remoteVideo.play();
			this.isPlaying = true;
		}

		_pause() {
			this.remoteVideo.pause(); 
            this.isPlaying = false;
		}
	
		private streamStarted(data:any) {
			switch(data.val().started) {
				case "yes":{
					var off = db.VIDEO_CONNECTED.push({uuid: this.uuid});
					off.remove();
					this.streamEnd = false;
                    //this.modal.close();
				}
				break;
				case "no":{
					if(this.stream != null){
                        this.streamEnd = true;
                        this._pause();
                        this.$forceUpdate();
						//this._pause();
						//--this.remoteVideo.pausercObject = null;
						//this.player.reset();
						//this.player.pause();
						//this.remoteVideo.src = null;
						//--this.stream = null;			
						//--this.connections = {};
                    //    if(!this.modal.opened()) this.modal.open();
					}
				}
				break;
				case "pause":{
					this.modalPause.open();
				}
				break;
				case "resume":{
					this.modalPause.close();
				}
				break;
			}
		}

		public getRemoteMediaStream(event:RTCTrackEvent) {
			this.stream = event.streams[0];
			////const remoteVideo = this.player.tech().el();
			this.remoteVideo.srcObject =  this.stream;
            this.remoteVideo.muted = false;
            //this._play();
		}

		private getPeerConnection(uuid:string) {
			if(uuid != this.uuid){
				if(this.connections[uuid]) return this.connections[uuid];
				var pc = new RTCPeerConnection(this.servers);
				this.connections[uuid] = pc;
				pc.addEventListener("icecandidate", (e:RTCPeerConnectionIceEvent) => {
					var off = db.VIDEO_SIGNAL.push({
						d: JSON.stringify({"by": this.uuid, "to": uuid, "ice": e.candidate, "type":"ice", "admin": 0})
					});
					off.remove();		
				});
				pc.addEventListener("track", (e:RTCTrackEvent) => this.getRemoteMediaStream(e));
				return pc;
			}
		}

		private handleSignal(data:any) {
			if(this.uuid != data.by){
				var pc = this.getPeerConnection(data.by);
				switch(data.type){
					case "offer":
						pc.setRemoteDescription(data.sdp)
							.then(() => pc.createAnswer())
							.then((answer:RTCSessionDescription) =>{
								pc.setLocalDescription(answer);
								var off = db.VIDEO_SIGNAL.push({
									d: JSON.stringify({"by": this.uuid, "to": data.by, "sdp":answer, "type": "answer", "admin": 0})
								});
								off.remove();				
							});
						break;
					case "ice":
						if(data.ice) pc.addIceCandidate(data.ice);
						break;
				}
			}
		}
}
