

































































































































    declare global {
        interface Window{ PrismicJS: any, PrismicDOM: any }
    }	

	import { Component, Vue } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';

	Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class Leadership extends Vue {
		prismicDOM = window.PrismicDOM;
		viewLeadership: boolean = false;
		leadership: any = [];
		currentLeader: any = {};
		
		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm._getPage(); });
		}

		_viewLeader(p:any ) {
			this.currentLeader = p;
			this.viewLeadership = true;
		}

		async _getPage() {
			let api = await _Prismic();
			this.leadership = (await api.query(window.PrismicJS.Predicates.at('document.type', 'about'),
				{ fetch:[ 'about.leadership' ]}
			)).results[0].data.leadership;
		}

		_scrollTo(id: string) {
			var el = document.getElementById(id);
			el.scrollIntoView({behavior: 'smooth'});
		}
}
