





























































































































	import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';

    declare global {
		interface Window { PrismicJS: any, videojs: any, tns: any, PrismicDOM: any }
	}

    Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class Sermons extends Vue {
		prismicDOM = window.PrismicDOM; 
		private remoteVideo: HTMLVideoElement;
		private currentMedia: any = {title:'', date:''};
		private isPlaying: boolean = false;
		private sermonListM:HTMLElement;
		private sermonListD:HTMLElement;
		private scrollLeftBtn:HTMLElement;
		private scrollRightBtn:HTMLElement;
		private mouseDown:number = -1; 

		private sermonPlaylist:HTMLElement;
		private scrollUpBtn:HTMLElement;
		private scrollDownBtn:HTMLElement;

/*
        private offerOptions: any ={ offerToReceiveVideo: 1, offerToReceiveAudio: 1 };
        private servers:any = {
            'iceServers': [{'urls': 'stun:stun.l.google.com:19302'}, 
            {'urls': 'turn:numb.viagenie.ca','credential': 'beaver','username': 'webrtc.websitebeaver@gmail.com'}]
		};
*/		

		programs: any = {};
		get hasProgram() {
			return Object.keys(this.programs).length == 0;
		}

		private audioVolume: number = 0.2;
		private connections:any = {};
		private player:any = null;
		//private uuid:string = shortid.generate();
		private paused:boolean = false;
		private streamEnd:boolean = false;
		private stream: MediaStream;

		private isProgram: boolean = true;

		page: number = 1;
		total: number = 0;
        sermons: any = [];
		first:string = null;
		currentIndex: number = 0;

		slideItems: NodeListOf<Element>;
		slideCategory:  NodeListOf<Element>;
		categories: any = ['audio', 'video'];
		middleSection: any = [];
		newStyle: any = [];

		@Watch('page') _fetch() { this._getSermons(); }

		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => vm._getPage());
		}

		public mounted() {
			this.player = window.videojs('video-id', {
				controls: true, responsive:true, liveui: true,
                controlBar: {pictureInPictureToggle: false}
			});

			this.remoteVideo = this.player.tech().el();

			this.player.controlBar.playToggle.on('click', (e) => {
				this.isPlaying? this._pause() : this._play();
			});
		}

		async _getPage() { this._getSermons(); }

		async _getSermons() {
			var api = await _Prismic();
			var { results, total_pages } = (
				await api.query(window.PrismicJS.Predicates.at('document.type', 'sermon'), { pageSize : 6, page : this.page })
			);
			this.sermons = [...this.sermons, ...results];
			if(!this.currentMedia.title) {
				this.setUpMedia(this.sermons[0]);
			}
		}

        viewMedia(item: any) {
			this.setUpMedia(item);
			this.player.play();
			this.isPlaying = true;
			this.isProgram = false;

			var el = document.getElementById('_body');
			el.scrollIntoView({behavior: 'smooth'});
		}

		setUpMedia(item) {
			this.currentMedia = {
				title: window.PrismicDOM.RichText.asText(item.data.title),
				date:  window.PrismicDOM.RichText.asText(item.data.date),
				location: window.PrismicDOM.RichText.asText(item.data.location),
				minister: window.PrismicDOM.RichText.asText(item.data.minister)
			};
			this.player.src({type: 'video/mp4', src: item.data.video.url});
		}

		beforeDestroy() {
			if(this.player) this.player.dispose();
		}

		_play() {
			this.remoteVideo.play(); 
			this.isPlaying = true;
		}

		_pause() {
			this.remoteVideo.pause; 
			this.isPlaying = false;
		}	
}
