




























































	declare global {
       interface Window { PrismicJS: any, PrismicDOM: any} 
    }
    
    import { Component, Vue } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	import Devotional from '@/components/devotional.vue';

	Component.registerHooks(['beforeRouteEnter']);

	@Component({components:{Devotional}})
	export default class VideoStream extends Vue {
		prismicDOM = window.PrismicDOM;
		devotional: any = {}; 
		related: any = [];	
		
		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm._getPage(); });
		}

		async _getPage() {
			var api = await _Prismic();
			this.devotional = await api.getByUID('devotional', this.$route.params.uid);
			this._getRelated(this.devotional.data.category[0].text)
		}


		async _getRelated(currentCategory: string) {
			var api = await _Prismic();
			var response = await api.query(
				window.PrismicJS.Predicates.fulltext('my.devotional.category', currentCategory),{ 
					fetch:['devotional.title', 'devotional.image', 'devotional.category', 'devotional.date'],
					pageSize : 6, page : 1
			});
			this.related = response.results;
		}

		 _formatDate(date: string) {
			const months = [
				"JANUARY", "FEBRUARY", "MARCH","APRIL","MAY", "JUN", "JULY", "AUGUST", "SEPTEMBER", "OCTOBER", "NOVEMBER", "DECEMBER"
				];
			var _date = new Date(date);
			return months[_date.getMonth()] + " " + _date.getDate()  + ", " + _date.getFullYear();
		}
	
}
